.PageLoading{width:100%;height:300px;overflow:hidden;display:flex;justify-content:center;align-items:center}

.circle{width:28px;height:28px;position:relative;border-radius:100%}.circle:before,.circle:after{content:'';width:100%;height:100%;border:4px solid transparent;border-top-color:#1da1f2;border-radius:100%;position:absolute;top:0;left:0}.circle:before{border:4px solid #105580}.circle:after{-webkit-animation:spin 0.8s linear infinite;animation:spin 0.8s linear infinite}@-webkit-keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

