.Register-Form-Group{background-color:#192734;border-radius:2px;text-align:left}.Register-Form-Group:focus-within .Register-Label{transition:all 0.15s ease-in-out;color:#1da1f2}.Register-Form-Group label{padding:5px 10px 0px 10px;color:#8899a6;margin-bottom:0}.Register-Form-Group .Register-Input{background-color:inherit;border:0;border-radius:inherit;border-bottom:2px solid #8899a6;font-size:19px;color:#fff;margin-top:0px;height:33px;padding:2px 10px 5px 10px}.Register-Form-Group .Register-Input:focus{box-shadow:none;border-color:#1da1f2}.Register-Btn{height:47px;font-weight:bold;margin:20px 0 30px 0;display:flex;justify-content:center}.Register-Btn:disabled{background-color:#105580;border-color:#105580}.Register-Btn .circle:after{border-top-color:#192734}.Register-Btn .circle:before{border:4px solid #105580}.Error .Register-Label{color:red}.Error .Register-Input{border-color:red}.Error-Message{color:red}

.Input-Container form{display:flex;justify-content:space-between}.Input-Text{font-size:20px;color:#545c64}.Tweet-btn{align-self:flex-start;font-weight:bold}.Tweet-btn:disabled{background-color:#105580;border-color:#105580}.Tweet-Area-Wrapper{min-height:70px;width:100%;position:relative;white-space:pre-line;word-break:break-word;color:transparent}.Tweet-Area{position:absolute;top:0;left:0;width:100%;height:100%;background:none;border:none;font-size:20px;padding:0;color:#fff;resize:none;word-break:break-word}.Tweet-Area:focus::-webkit-input-placeholder{color:#a1a9b0}.Tweet-Area:focus:-ms-input-placeholder{color:#a1a9b0}.Tweet-Area:focus::-ms-input-placeholder{color:#a1a9b0}.Tweet-Area:focus::placeholder{color:#a1a9b0}.Tweet-Area:focus{background:none;border:none;font-size:20px;padding:0;box-shadow:none;color:#fff}.Chars-Count{color:#545c64}.text-red{color:#e2264d}@media (max-width: 576px){.Input-Container form{display:block}.Tweet-btn{margin-top:10px}}

