.Likes-Container{display:flex;justify-content:center;align-items:center}.text-red{color:#e2264d}.button.fav{width:50px;height:50px;cursor:pointer}.fav{background-image:url(/static/media/corazon-like.313656d4.png);background-size:cover}.fav.is-liked{-webkit-animation:like 1s steps(28);animation:like 1s steps(28);-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}@-webkit-keyframes like{0%{background-position:0 0}100%{background-position:right}}@keyframes like{0%{background-position:0 0}100%{background-position:right}}

.Tweet-Container{display:flex;padding:25px 20px;-webkit-animation:fadeIn 0.5s;animation:fadeIn 0.5s}@-webkit-keyframes fadeIn{0%{opacity:0;transform:translateY(-20px)}100%{opacity:1;transform:translateY(0px)}}@keyframes fadeIn{0%{opacity:0;transform:translateY(-20px)}100%{opacity:1;transform:translateY(0px)}}.Tweet-Container:hover{background-color:#17232e}.Tweet-Container .Avatar{width:70px;height:70px;border-radius:50%;overflow:hidden;background-color:#545c64}.Tweet-Header{font-weight:bold;margin-bottom:10px}.Tweet-Header .UserName{color:#545c64;font-weight:400;margin-left:5px}.Tweet-Header .Tweet-Info{font-weight:400;color:#545c64;margin-left:5px}.Delete-Svg{width:18px;height:18px;fill:#8899a6;opacity:0.3;cursor:pointer;transition:all 0.28s}.Delete-Svg:hover{fill:red;opacity:1}.Tweet-Actions{display:flex;flex-direction:column;justify-content:center;align-items:center}@media (max-width: 576px){.Tweet-Container{padding:25px 0}.Tweet-Avatar{width:40px;height:40px}}

.User-Header-Container{display:flex;justify-content:flex-start;align-items:center}.Header-User{font-weight:900;font-size:19px}.Header-Tweets{color:#8899a6;font-size:13px}.User-Header-Container .arrow{background-color:#1da1f2;transform:scaleX(-1);width:18px;height:2px;border-radius:1px;margin-right:30px}.User-Header-Container .arrow::after{border-color:#1da1f2;border-width:2px;height:12px;width:12px;top:-5px;border-radius:1px}

.User-Profile{margin-bottom:3em}.User-Profile .Avatar{width:134px;height:134px;margin-bottom:1rem}.Profile-Name{font-weight:900;font-size:19px}.Profile-UserName{color:#8899a6;font-size:15px}

